<template>
	<div style="width: 100%;height: 100%;position: fixed;left: 0;top: 0;z-index: 100;font-size: 30px;opacity: .5;display: none;background: rgba(0,0,0,.5);" id="windowHints">
		<div style="position: absolute;left: 48%;top: 48%;padding: 30px;background: #fff; border-radius: 10px;">{{Msg}}</div>
	</div>
</template>

<script>
	import EventBus from '@/assets/scripts/common/EventBus';
	export default {
		data() {
			return {
				Msg: ''
			}
		},
		created() {
			const that = this;
			EventBus.$on('windowHints', function(msg) {
				that.Msg = msg;
				$('#windowHints').fadeIn();
				setTimeout(function() {
					$('#windowHints').fadeOut();
				}, 1000);
			});
		},
		beforeDestroy() {
			this.$off('windowHints');
		}
	}
</script>

<style>

</style>


