<template>
	<div>
		<router-link to="/" class="game-return hovers"></router-link>
		<!--游戏名称-->
		<div class="game-top">
			<div class="game-title txt-ct">
				<img src="/static/images/Interface/GameRoom_Top_RoomTitle01.png" height="102" />
				<div class="game-multiple uppercase-number txt-rt">
					<i v-for="item in multipleArr" :style='"background-position-y:"+(item-1)*-27+"px;"'></i>
					<i style="background-position-y: -351px;">倍</i>
				</div>
			</div>
		</div>
		<!--侧边栏-->
		<game-side></game-side>
		<!--奖池金额-->
		<div class="txt-ct game-jackpot">
			<div class="game-bonus small-number">
				<div class="txt-lf">
					<i style="background-position-y: -36px;">1</i>
					<i style="background-position-y: -72px;">2</i>
					<i style="background-position-y: -108px;">3</i>
					<i style="background-position-y: -144px;">4</i>
				</div>
			</div>
			<div class="game-doubt hovers"></div>
		</div>
		<!--游戏区-->
		<div class="game-content">
			<i class="game-cont-bg game-bg-btm"></i>
			<div class="game-cont-bg game-bg-top"></div>
			<!--game-->
			<div class="game-region">
				<div class="game-list" v-for="item in numbList">
					<ul class="game-boxs">
						<li :style='"background-image: url("+numbBG+");"' v-for="items in item.numLists">
							<div class="game-list-imgs" v-if="!item.showVague">
								<img :src='numbHref+items.element+"/"+items.element+"-"+currentNumber+".png"' width="164" height="164" v-if="items.run==1" />
								<img :src='numbHref+items.element+"/"+items.element+"-0.png"' width="164" height="164" v-if="items.run==0" />
							</div>
							<div class="game-list-imgs" v-if="item.showVague">
								<img :src='"/static/images/numbers/vague/"+items.element+".png"' width="164" height="164" />
							</div>
							<!--动画1-->
							<div class="game-list-effect2" v-if="isAnimate && items.run==1">
								<img :src='"/static/images/SpecialEffect/Effect/"+currentEffect+".png"' width="286" height="286" />
							</div>
							<!--动画2-->
							<div class="game-list-effect" v-if="isAnimate2 && items.run==1">
								<img :src='"/static/images/SpecialEffect/CLAMP/"+currentEffect+".png"' width="200" height="200" />
							</div>
						</li>
					</ul>
				</div>
			</div>
			<!--game end-->
			<div class="game-canvas" v-show="showCanvas">
				<canvas id="game-line" width="1400" height="640"></canvas>
			</div>
			<div class="game-numbers game-cont-lf">
				<b v-for="item in Numbers">{{item}}</b>
			</div>
			<div class="game-numbers game-cont-rt">
				<b v-for="item in Numbers">{{item}}</b>
			</div>
		</div>
		<div class="game-bottom">
			<!--压线-->
			<b class="game-pressing-line">{{PressingLine}}</b>
			<!--总压分-->
			<div class="game-total-pressure small-number narrow-number">
				<i style="background-position-y: -36px;">1</i>
				<i style="background-position-y: -72px;">2</i>
				<i style="background-position-y: -108px;">3</i>
				<i style="background-position-y: -144px;">4</i>
			</div>
			<!--赢分-->
			<div class="game-win-points small-number narrow-number">
				<i style="background-position-y: -36px;">1</i>
				<i style="background-position-y: -72px;">2</i>
				<i style="background-position-y: -108px;">3</i>
				<i style="background-position-y: -144px;">4</i>
			</div>
			<div class="game-btm-bottom">
				<div class="game-user-infor clearfix">
					<div class="game-user-head pull-left">
						<img src="/static/images/hall/GameHall_Bottom_Picture.png" width="109" height="109" />
						<div class="game-grade">v19</div>
					</div>
					<dl class="pull-left">
						<dt>昵称上限六字</dt>
						<dd>
							<div class="small-number narrow-number dis-inl-blk">
								<i v-for="item in PersonalScoreArr" :style='"background-position-y:"+(item)*-36+"px;"'>{{item}}</i>
							</div>
							<b class="game-user-plus hovers"></b>
						</dd>
					</dl>
				</div>
				<!--压分-->
				<div class="add-subtrac clearfix">
					<b class="Subtraction pull-left hovers" @click="_Subtraction"></b>
					<div class="Pressure small-number narrow-number pull-left">
						<i v-for="item in scoreArr" :style='"background-position-y:"+(item)*-36+"px;"'>{{item}}</i>
					</div>
					<b class="bonus-points pull-left hovers" @click="BonusPoints"></b>
				</div>
				<!--开始按钮-->
				<b class="game-play hovers" @click="PlayGame"></b>
			</div>
		</div>
		<!--提示窗口-->
		<window-hints></window-hints>
	</div>
</template>

<script>
	import GameSide from '@/components/sub/gameSide/GameSide';
	import WindowHints from '@/components/sub/windowHints/windowHints';

	import { Numbers } from '@/assets/scripts/common/Numbers';
	import { randomNumber } from '@/assets/scripts/js/randomNumber';
	import { coordinate } from '@/assets/scripts/common/coordinate';

	import ws from "@/assets/scripts/api/websocket";
	import EventBus from '@/assets/scripts/common/EventBus';
	export default {
		data() {
			return {
				//游戏两侧数字
				Numbers: Numbers,
				//游戏初始化展示
				numbList: [{
						showVague: false,
						numLists: []
					},
					{
						showVague: false,
						numLists: []
					},
					{
						showVague: false,
						numLists: []
					},
					{
						showVague: false,
						numLists: []
					},
					{
						showVague: false,
						numLists: []
					},
				],
				dataObj: null,
				//数字路径
				numbHref: '/static/images/numbers/',
				//数字背景路径
				numbBG: '/static/images/Interface/GameRoom_Middle_GamblingTable_IconPicBg.png',
				vagueBG: '/static/images/numbers/vague/0.png',
				//数字旋转
				currentNumber: 0,
				//中奖动画
				isAnimate: false,
				isAnimate2: false,
				currentEffect: 0,
				showCanvas: false,
				//是否可以开始游戏
				play: true,
				//压线
				PressingLine: 9,
				//加减分展示
				score: 0,
				scoreArr: [],
				//倍数
				multiple: 10,
				multipleArr: [],
				//个人分数
				PersonalScore: 5000,
				PersonalScoreArr: []
			}
		},
		created() {
			//页面初始化展示
			this.Initialization();
			//加减分展示
			this.scoreArr = this.IntegralConversion(this.score);
			//个人分数展示
			this.PersonalScoreArr = this.IntegralConversion(this.PersonalScore);
			//倍数展示
			this._Multiple(this.multiple);
		},
		mounted() {
			ws.onerror = function() {
				console.log("WebSocket连接发生错误");
			};
			ws.onopen = function() {
				console.log("WebSocket连接成功");
			};
		},
		methods: {
			//页面初始化
			Initialization() {
				const that = this;
				//在十五个空格内填充初始化数字
				for(let i = 0; i < that.numbList.length; i++) {
					for(let j = 0; j < 3; j++) {
						that.numbList[i].numLists.push({
							element: randomNumber(15),
							run: 0
						});
					}
				}
			},
			//开始游戏
			PlayGame() {
				if(this.score <= 0) {
					//未压分
					EventBus.$emit('windowHints', '请下注!');
					return;
				}
				const that = this;
				ws.send('{"type": "game","user_id": 1,"room_id": 1,"money": 300}');
				ws.onmessage = function(event) {
					that.dataObj = JSON.parse(event.data);
					console.log(that.dataObj);
				};
				if(this.play) {
					$('.game-list').each(function(e) {
						setTimeout(function() {
							that._PlayGame('.game-region', e);
						}, e * 300);
					});
				}
			},
			_Presupposition() {
				this.play = false;
				this.currentNumber = 0;
				this.currentEffect = 0;
				//中奖线路展示
				this.showCanvas = false;
				//中奖动画
				this.isAnimate = false;
				this.isAnimate2 = false;
				//清除画布
				this.GameLine();
				//加减分清零
				this.score = 0;
				this.scoreArr = this.IntegralConversion(this.score);
			},
			_PlayGame(id, index) {
				//预设
				this._Presupposition();
				const that = this;
				let box = $(id).children().eq(index).find('ul');
				let h = box.height();
				box.css({
					'height': 2 * h + 'px',
					'top': -h + 'px'
				});
				let current_h = h;
				let speed = 0; //判断动画执行速度
				//动画循环次数
				let frequency = 10,
					frequencys = 0;
				//生成随机的动画数字
				RandomNumbs(index);

				function RandomNumbs(index) {
					if(frequencys >= frequency) {
						//最后一次滚动，把后台返回的数字填入显示
						for(let i = 2; i >= 0; i--) {
							let j = index + i * 5;
							that.numbList[index].numLists.unshift(that.dataObj.data_look[j]);
						}
					} else {
						for(let i = 0; i < 3; i++) {
							that.numbList[index].numLists.unshift({
								element: randomNumber(15),
								run: 0
							});
						}
					}
				}
				let setAnimation = setInterval(function() {
					switch(speed) {
						case 0:
							//动画缓慢开始
							current_h = current_h - 6400 / current_h;
							//数字模糊
							that.numbList[index].showVague = true;
							//动画开始，背景变模糊
							box.find('li').css('background-image', 'url(' + that.vagueBG + ')');
							break;
						case 1:
							//动画进行时，快速动画
							current_h = current_h - 30;
							break;
						case 2:
							//动画缓慢结束
							current_h = current_h - current_h / 20;
							//显示最后数据
							if(Math.floor(current_h) < 1) {
								//数字正常
								that.numbList[index].showVague = false;
								//动画结束，背景变正常
								box.find('li').css('background-image', 'url(' + that.numbBG + ')');
								current_h = 0;
								box.height(h);
							}
							break;
					}
					box.animate({
						'top': -current_h + 'px'
					}, 0, function() {
						if(current_h <= 0) {
							speed = 1;
							for(let i = 3; i < 6; i++) {
								//动画每次循环结束，都删除一组数字，为下次循环生成的新数字留位
								that.numbList[index].numLists.splice(i, 3);
							}
							frequencys++;
							if(frequencys > frequency) { //动画循环多少次后执行最后一次动画显示需要的数据
								speed = 2;
							} else {
								RandomNumbs(index);
								current_h = h;
								box.css('top', -current_h + 'px');
							}
							return;
						}
					});
					if(speed == 2 && that.numbList[that.numbList.length - 1].showVague == false) {
						//结束动画
						clearInterval(setAnimation);
						box.css('top', '0');
						speed = 0;
						frequency = 0;
						//继续游戏
						that.play = true;
						return;
					}
				}, 10);
			},
			//数字旋转
			_currentNumber() {
				const that = this;
				if(that.isAnimate) {
					let setIns = setInterval(function() {
						that.isAnimate ? true : clearInterval(setIns);
						that.currentNumber++;
						if(that.currentNumber > 12) {
							that.currentNumber = 0;
						}
					}, 100);
				}

			},
			//中奖效果
			_currentEffect(l) {
				const that = this;
				if(that.isAnimate) {
					let len = l;
					let setIns = setInterval(function() {
						that.isAnimate ? true : clearInterval(setIns);
						that.currentEffect++;
						if(that.currentEffect > 21) {
							that.isAnimate2 = false;
						}
						if(that.currentEffect > len) {
							that.currentEffect = 0;
						}
					}, 100);
				}
			},
			//积分数字转换为数组
			IntegralConversion(scores) {
				return scores.toString().split('');
			},
			//加分
			BonusPoints() {
				if(!this.play) {
					//游戏正在进行
					return;
				}
				if(this.PersonalScore < this.PressingLine * this.multiple) {
					//个人当前积分小于最小加注数
					EventBus.$emit('windowHints', '您的积分不足，请充值!');
					return;
				}
				if(this.score - this.PressingLine * this.multiple > this.PersonalScore) {
					//个人当前积分无法多次加注
					EventBus.$emit('windowHints', '您的积分不足，请充值!');
					return;
				}
				//最多只能加注5次
				const f = 5;
				if(this.score >= this.PressingLine * this.multiple * f) {
					EventBus.$emit('windowHints', '当前游戏只能下注' + this.score + '积分');
					return;
				}
				//个人分数减少
				this.PersonalScore = this.PersonalScore - this.PressingLine * this.multiple;
				this.PersonalScoreArr = this.IntegralConversion(this.PersonalScore);
				//加分
				this.score = this.score + this.PressingLine * this.multiple;
				this.scoreArr = this.IntegralConversion(this.score);

			},
			//减分
			_Subtraction() {
				if(!this.play) {
					//游戏正在进行
					return;
				}
				if(this.score <= 0) {
					return;
				}
				//减分
				this.score = this.score - this.PressingLine * this.multiple;
				this.scoreArr = this.IntegralConversion(this.score);
				//个人分数加回
				this.PersonalScore = this.PersonalScore + this.PressingLine * this.multiple;
				this.PersonalScoreArr = this.IntegralConversion(this.PersonalScore);
			},
			//图片显示倍数
			_Multiple(m) {
				this.multipleArr = m.toString().split('');
				if(m < 100 && m >= 10) {
					if(m % 10 == 0) {
						if(m > 10) {
							this.multipleArr.splice(1, 1, '10');
						} else {
							this.multipleArr.splice(0, 2, '10')
						}
					} else {
						if(m > 20) {
							this.multipleArr.splice(1, 0, '10');
						} else {
							this.multipleArr.splice(0, 2, '10', m - 10);
						}
					}
					return;
				}
				if(m < 1000 && m >= 100) {
					this.multipleArr.splice(1, 2, '11');
					return;
				}
				if(m < 10000 && m >= 1000) {
					this.multipleArr.splice(1, 3, '12');
					return;
				}
				if(m >= 10000) {
					this.multipleArr.splice(1, 4, '13');
					return;
				}
			},
			GameLine(data) {
				const canvas = document.getElementById('game-line');
				const ctx = canvas.getContext('2d');
				if(this.showCanvas) {
					ctx.lineWidth = 10;
					for(let z = 0; z < data.length; z++) {
						let i = data[z] - 1;
						ctx.beginPath();
						ctx.strokeStyle = coordinate[i].color;
						ctx.lineJoin = "round";
						ctx.shadowOffsetX = 0; // 阴影Y轴偏移
						ctx.shadowOffsetY = 0; // 阴影X轴偏移
						ctx.shadowBlur = 24; // 模糊尺寸
						ctx.shadowColor = coordinate[i].color; // 阴影颜色
						ctx.moveTo(coordinate[i].zb[0].x, coordinate[i].zb[0].y);
						for(let j = 1; j < coordinate[i].zb.length; j++) {
							ctx.lineTo(coordinate[i].zb[j].x, coordinate[i].zb[j].y);
						}
						ctx.stroke();
					}
				} else {
					ctx.clearRect(0, 0, canvas.width, canvas.height);
				}

			}
		},
		watch: {
			play(p) {
				//游戏动画结束后执行中奖动画
				if(p) {
					if(!this.dataObj.isWin) {
						EventBus.$emit('windowHints', '您未中奖，请继续加油!');
						return;
					}
					EventBus.$emit('windowHints', '恭喜您中奖了！');
					this.showCanvas = true;
					this.isAnimate = true;
					this.isAnimate2 = true;
					//文字旋转
					this._currentNumber();
					//中奖动画
					this._currentEffect(63);
					//中奖路线
					this.GameLine(this.dataObj.line_look);
				} else {
					//预设
					this._Presupposition();
				}
			}
		},
		components: {
			GameSide,
			WindowHints
		},
		destroyed() {
			ws.close() //离开路由之后断开websocket连接     
		}
	}
</script>

<style>

</style>


