<template>
	<div class="main-layout main-layout-bg">
		<div class="main-top"></div>
		<router-view></router-view>
	</div>
</template>

<script>
	export default {
		mounted() {
			let h, h1;
			h = $(window).height();
			$(window).resize(function() {
				h1 = $(window).height();
				h = h > h1 ? h : h1;
			});
			const c_h = 1080;
			let p = Math.floor((h / c_h) * 10) / 10;
			let mgt = (c_h - h) / 2;
			console.log(h, p, mgt);
			$('.main-layout').css({
				'transform': 'scale(' + p + ')',
				'margin-top': -mgt + 'px',
			});
		}
	}
</script>

<style>
	@import '../../../assets/styles/reset';
	@import '../../../assets/styles/styles';
</style>


